export default{
	data() {
		return {
			time: "2020-03-25",
			start: "",
			room: {},
			meet: [],
			meetIndex: 0,
			number: -1,
			name: '',
			phone: -1,
			ImgUrl: this.ImgUrl
		}
	},
	methods: {
		changeTime(e) {
			this.time = e.target.value
		},
		
		roomchange(e) {
			this.meetIndex = e.target.value
		},
		
		numberValue(e){
			this.number = e.target.value
		},
		
		nameValue(e){
			this.name = e.target.value
		},
		
		phoneValue(e){
			this.phone = e.target.value
		},
		
		// 获取当前日期
		getToday(){
			let now = new Date()
			let year = now.getFullYear()
			let month = now.getMonth() + 1
			let day = now.getDate()
			if (month < 10) {
				month = '0' + month
			}
			if (day < 10) {
				day = '0' + day
			}
			this.time = year + '-' + month + '-' + day
			this.start = year + '-' + month + '-' + day
		},
		
		// 获取区域
		getAres() {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getArea',
				success(res) {
					res.data.data.forEach(function(item){
						that.room[item.dict_name] = {id:item.dict_id}
					})
					
					that.getMeet()
				}
			})
		},
		
		// 获取场地
		getMeet() {
			let baseUrl = this.WebUrl
			let that = this
			for(let key in this.room){
				uni.request({
					url: baseUrl + '/getInfor',
					data: {
						area_id: that.room[key].id
					},
					success(res) {
						console.log(res)
						res.data.data.forEach(function(item){
							that.room[key][item.infor_id] = item.name
							that.meet.push(item.name)
						})
					}
				})
			}
		},
		
		// 提交需求
		addNeed() {
			let baseUrl = this.WebUrl
			let that = this
			let area_id = ''
			let infor_id = ''
			for(let key in this.room) {
				for(let item in this.room[key]){
					if(this.room[key][item] === this.meet[this.meetIndex]){
						infor_id = this.room[key].id
					}
				}
			}
			if (this.number <= 0){
				uni.showModal({
					title: "提示",
					content: "请输入人数",
					showCancel: false
				})
			}else if (this.name === ''){
				uni.showModal({
					title: "提示",
					content: "请输入联系人名字",
					showCancel: false
				})
			}else if (!(/^1[3456789]\d{9}$/.test(this.phone))){
				uni.showModal({
					title: "提示",
					content: "手机号码有误，请重新输入",
					showCancel: false
				})
			}else {
				uni.request({
					url:baseUrl + '/addNeed',
					method: 'POST',
					data: {
						area_id: area_id,
						cost_time: that.time,
						infor_id: infor_id,
						num: that.number,
						contacts: that.name,
						phone: that.phone
					},
					success: (res) => {
						uni.showModal({
							title: "提示",
							content: res.data.msg,
							showCancel: false,
							success: (resd) => {
								if (resd.confirm) {
									uni.navigateBack({
										delta: 1
									})
								}
							}
						})
					}
				})
			}
		}
	},
	onLoad: function(){
		this.getAres()
		
		this.getToday()
	}
}
